.jumbotron {
  padding: 100px;
}

.card {
    
    border-radius: 2px;

    margin: 1rem;
 
   width: auto;
    box-shadow: 0 6px 12px #fdc4b6, 0 12px 6px #fdc4b6;
    text-align: left;
}  
.list-group-item p {
  font-size: 16px;
}
.results {

  background: #ceefe4;
  height: 100%;
  padding: top 10px;
  display: inline-flex;
  flex-flow: row wrap;
  justify-content: space-around;
  align-content: flex-start;
  overflow: auto;
}

.btn-container {
  float: right;
}

@media (max-width: 767px) {
  .btn-container {
    
  }
}

.footer {
    background:  #ceefe4;
    height: 10px;
    position: relative;
    color: #114357;
    overflow: none;
  }
  
 
.wrapper {

    background: #ceefe4;
    height: 100%;
  
    display: inline-flex;
    flex-flow: row wrap;
    justify-content: space-around;
    align-content: flex-start;
    overflow: auto;
   
  }
  
  
.navbar {
  border-radius: 0;
}

